@import 'null';

@import url('https://fonts.googleapis.com/css2?family=Raleway:wght@300;400;500&display=swap');

html,
body,
#root {
    height: 101%;
    overflow: hidden;
}

#root {
    --index: calc(1vw + 1vh);
}

.main {
    &__page {
        position: relative;
        height: 100%;
    }

    &__button {
        position: absolute;
        width: 54px;
        height: 54px;
        // content: '';
        // font-weight: 600;
        top: 54px;
        left: 54px;
        z-index: 5;
        font-size: 34px;
        color: white;
        display: flex;
        justify-content: center;
        align-items: center;
        padding-bottom: 4px;
        transition: all .2s ease 0s;
        cursor: pointer;
        will-change: transform;


        &::before {
            position: absolute;
            content: '';
            width: 100%;
            height: 100%;
            top: 0;
            left: 0;
            border: 2px solid white;
            transition: all .2s ease 0s;
        }

        &_active {
            position: relative;
            width: 100%;
            height: 100%;

            &::before {
                background: white;
                position: absolute;
                content: '';
                width: 27px;
                height: 2px;
                top: 25.5px;
                left: 13.5px;
                transform: rotateZ(45deg);
                transition: all .2s ease 0s;
            }

            &::after {
                background: white;
                position: absolute;
                content: '';
                width: 27px;
                height: 2px;
                top: 25.5px;
                left: 13.5px;
                transform: rotateZ(-45deg);
                transition: all .2s ease 0s;
            }
        }



        @media(any-hover:hover) {
            &:hover {
                background: white;
                color: black;
                transform: scale(1.2);

                // border: 2px solid white;
                // transition: .1s;
                &::before {
                    transform: rotateZ(90deg);
                }

                & .main__button_active {
                    &::before {
                        background: black;
                    }

                    &::after {
                        background: black;
                    }
                }


            }
        }

        &:active {
            transform: rotateZ(180deg);
            transition: all .2s ease 0s;
            // background: red;
        }

    }

    &__text {
        &_wrapper {
            position: absolute;
            width: 100%;
            height: 100%;
            top: 0;
            left: 0;
            display: flex;
            justify-content: center;
            align-items: center;
            transition: all .4s ease 0s;
            z-index: 4;
        }

        width: calc(var(--index)*25);
        height: calc(var(--index)*18);
        color: white;
        z-index: 4;
        justify-content: center;
        align-items: center;
        display: flex;
        font-family: 'Raleway',
        sans-serif;
        font-weight: 300;
        padding: 10px 20px;
        line-height: 1.5;
        font-size: calc(var(--index)* 1.44);
        text-align: center;

        background: rgba(255, 255, 255, 0.2);
        border-radius: 16px;
        box-shadow: 0 4px 30px rgba(0, 0, 0, 0.1);
        backdrop-filter: blur(4px);
        -webkit-backdrop-filter: blur(5px);
        border: 1px solid rgba(0, 0, 0, 0.3);
    }
}